import PropTypes from 'prop-types';
import React, {useState} from 'react'

export default function Textarea(props) {
  
  const handle=function(){
  let  upper=Text.toUpperCase();
  setText(upper);
  props.showAlert("convert to upper" , "warning")
  props.toggleMode("danger");
  }
  const handleonchange=function(event){
  setText(event.target.value)
  }
  const [Text,setText]=useState(" ");  // Use state
  return (
<div className="mb-3">
  <h1 style={{color:props.mode==='light'?'black':'white'}}>{props.heading}</h1>
  <label for="text" class="form-label" style={{color:props.mode==='light'?'black':'white'}}>  Example textarea </label>
  <textarea className="form-control"     value={Text}   onChange={handleonchange}   style={{backgroundColor:props.mode==='light'?'grey':'white',  color:props.mode==='light'?'white':'black'}}  id="exampleFormControlTextarea1" rows="8 "> </textarea>
  <button className="btn btn-primary"  onClick={handle} >Convert to upper case</button> 
  <h2 style={{color:props.mode==='light'?'black':'white'}}>{Text.length>0?Text:"Enter something here"}</h2>
  <p style={{color:props.mode==='light'?'black':'white'}}>{Text.split(" ").length} words and {Text.length} characters </p>
</div>
  );
} 
