
import './App.css';
import Navbar from './component/Navbar';
import Textarea from './component/Textarea';
import Home from './component/Home';
import  React, {useState} from 'react'
import Pos from  './component/Pos'


//import {
  //BrowserRouter as Router,
  //Routes,
  //Route,
 // Link
//} //from "react-router-dom";

function App() {  
const[mode, setmode]=useState("light")      //buydefault
const[alert,setAlert]=useState("null")      



const showAlert= function(message,type){
setAlert({
msg:message, 
Type:type
}); 
setTimeout(() => {
setAlert("null")
}, 3000);
} 
const removeBody=()=>{
document.body.classList.remove('bg-success')
document.body.classList.remove('bg-primary')
document.body.classList.remove('bg-danger')
}

const toggleMode= function(cls){
removeBody();
document.body.classList.add('bg-'+cls)
if(mode==="light"){
setmode("dark")
showAlert("dark mode enable","warning")
document.title="Dark mode enable" 
}
else{
setmode("light")
showAlert("Light mode enable","success")
document.title="Light mode enable"
}
}
  return (
<>

<Navbar title="Utils" about="about us"  mode={mode}  toggleMode={toggleMode}/>
<Pos alert={alert}   />
<div className="container">

<Textarea heading="Enter the text analize"  mode={mode}  showAlert={showAlert}  toggleMode={toggleMode}/>
</div>

</> 
);
}
export default App;
