import React from 'react'
import PropTypes from 'prop-types'
//import { Link } from 'react-router-dom'


export default function Navbar(props) {
  return (
    <nav className={'navbar navbar-expand-lg navbar-' + props.mode + ' bg-' + props.mode}>
    <div className="container-fluid"> 
      <a className="navbar-brand" href="/">{props.title}</a>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="#navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <div className="nav-item">
            <div className="nav-link active" aria-current="page" href="#">{props.about}</div>
          </div>
          <li className="nav-item">
            <div className="nav-link" href="#">HOME</div>
          </li>
          <li className="nav-item dropdown">
            <a className="nav-link dropdown-toggle"  to="/" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              Dropdown
            </a>
            <ul className="dropdown-menu" aria-labelledby="navbarDropdown" >
              <li><a className="dropdown-item" href="/">Action</a></li>
              <li><a className="dropdown-item" href="/">Another action</a></li>
              <li><hr className="dropdown-divider"/></li>
              <li><a className="dropdown-item" href="/">Something else here</a></li>
            </ul>
          </li>
          <li className="nav-item">
            <a className="nav-link disabled" href="/"  aria-disabled="true">Disabled</a>
          </li>
        </ul>
        
      </div>
    <button type="button" class="btn btn-primary" onClick={()=>props.toggleMode("primary")}>Primary</button>
    <button type="button" class="btn btn-success"onClick={()=>props.toggleMode("success")}>Success</button>
    <button type="button" class="btn btn-danger" onClick={()=>props.toggleMode("danger")}>Danger</button>


      <input type="checkbox" className="btn-check" onClick={()=>props.toggleMode("primary")}  id="btn-check" autocomplete="off"/>
    <label className="btn btn-danger"  for="btn-check" >{'Enable ' + props.mode + ' Mode'}</label> 
    </div>
  </nav> 
  )
}
Navbar.propTypes = {
  title: PropTypes.string,
  about:PropTypes.string
};
Navbar.defaultProps={
   title:'set up',
   about:'low'
}